@mixin dl_style() {
    dl {
        dt {
            margin-bottom : 10px;
            font-family   : Poppins;
            font-size     : 23px;
            font-style    : normal;
            font-weight   : 700;
            line-height   : 35px;
            letter-spacing: 0em;
            text-align    : center;

        }

        %font-shared {
            font-family   : Poppins;
            font-style    : normal;
            line-height   : 26px;
            letter-spacing: 0em;
            text-align    : center;
            font-weight   : 600;
        }

        dd {
            span {
                @extend %font-shared;
                color: #0D3840;

                a {
                    margin-left: 5px;
                    color      : #F15A24;
                }
            }
        }
    }
}

.loginwrap {
    display            : block;
    padding-top        : 5px;
    width              : 100%;
    height             : 1080px;
    color              : white;
    margin             : 0;
    box-sizing         : border-box;
    background-position: center;
    background-repeat  : no-repeat;
    background-size    : 100% 100%;
    background-image   : url('../../assets/login/back.png');
}


.login_page4 {
    .loginformwrap {
        width: 586px !important;
    }
}

.logincontent {
    // min-height: 780px;
    position: absolute;

    .loginformwrap {
        display         : block;
        margin          : 0 auto;
        padding         : 24px;
        width           : 414px;
        background-color: #01191E;
        border-radius   : 10px;
        text-align      : center;

        .form_group {
            margin-top: 25px;
            text-align: left;

            .input_wrap {
                position  : relative;
                margin-top: 5px;
            }

            .input {
                position        : relative;
                margin-top      : 5px;
                display         : block;
                width           : 100%;
                background-color: #0D3840 !important;
                border          : none !important;
                border-radius   : 5px;
                outline         : none;
            }

            input {
                padding-left: 10px;
                height      : 46px;
                color       : #fff;
                padding     : 1px 0px 2px 12px !important;
            }

            a {
                float: right;
                color: #F15A24;
            }


            ul {
                display: flex;
                margin : 10px -3px;

                li {
                    margin       : 0 3.5px;
                    width        : 25%;
                    border-bottom: 6px solid #01060A;
                    border-radius: 5px;
                }
            }

            p {
                font-family   : Poppins;
                font-size     : 13px;
                font-style    : normal;
                font-weight   : 400;
                line-height   : 20px;
                letter-spacing: 0em;
                color         : #0D3840;
            }

            .remember {
                display    : flex;
                margin-top : 25px;
                align-items: center;

                input {
                    margin-right: 5px;
                    ;
                    width           : 30px;
                    height          : 30px;
                    background-color: #01060A;
                    border-radius   : 8px !important;
                    cursor          : pointer;
                }

                span {
                    line-height   : 30px;
                    font-family   : Poppins;
                    font-size     : 17px;
                    font-style    : normal;
                    font-weight   : 600;
                    line-height   : 26px;
                    letter-spacing: 0em;
                    text-align    : left;

                }

                input[type='checkbox'].remember_ch,
                input[type='radio'] {

                    --active          : #01060A;
                    --active-inner    : #fff;
                    // --focus: 2px rgba(39, 94, 254, .3);
                    // --border: #2a315c;
                    --border-hover    : #01060A;
                    --background      : #01060A;
                    --disabled        : #F6F8FF;
                    --disabled-inner  : #E1E6F9;
                    -webkit-appearance: none;
                    -moz-appearance   : none;
                    height            : 30px;
                    outline           : none;
                    display           : inline-block;
                    vertical-align    : top;
                    position          : relative;
                    margin            : 0;
                    cursor            : pointer;
                    border            : 1px solid var(--bc, var(--border));
                    background        : var(--b, var(--background));
                    transition        : background .3s, border-color .3s, box-shadow .2s;
                    margin-right      : 5px;

                    &:after {
                        content   : '';
                        display   : block;
                        left      : 0;
                        top       : 0;
                        position  : absolute;
                        transition: transform var(--d-t, .3s) var(--d-t-e, ease), opacity var(--d-o, .2s);
                    }

                    &:checked {
                        --b    : var(--active);
                        --bc   : var(--active);
                        --d-o  : .3s;
                        --d-t  : .6s;
                        --d-t-e: cubic-bezier(.2, .85, .32, 1.2);
                    }

                    &:disabled {
                        --b    : var(--disabled);
                        cursor : not-allowed;
                        opacity: .9;

                        &:checked {
                            --b : var(--disabled-inner);
                            --bc: var(--border);
                        }

                        &+label {
                            cursor: not-allowed;
                        }
                    }

                    &:hover {
                        &:not(:checked) {
                            &:not(:disabled) {
                                --bc: var(--border-hover);
                            }
                        }
                    }

                    &:focus {
                        box-shadow: 0 0 0 var(--focus);
                    }

                    &:not(.switch) {
                        width: 30px;

                        &:after {
                            opacity: var(--o, 0);
                        }

                        &:checked {
                            --o: 1;
                        }
                    }

                    &+label {
                        font-size     : 14px;
                        line-height   : 21px;
                        display       : inline-block;
                        vertical-align: top;
                        cursor        : pointer;
                        margin-left   : 4px;
                    }
                }

                input[type='checkbox'] {
                    &:not(.switch) {
                        border-radius: 7px;

                        &:after {
                            width      : 5px;
                            height     : 13px;
                            border     : 2px solid var(--active-inner);
                            border-top : 0;
                            border-left: 0;
                            left       : 11px;
                            top        : 4px;
                            transform  : rotate(var(--r, 20deg));
                        }

                        &:checked {
                            --r: 43deg;
                        }
                    }
                }
            }
        }

        @include dl_style();

        ul {
            list-style: none;

            li {
                margin-top: 25px;

                .button {
                    display         : block;
                    width           : 100%;
                    height          : 46px;
                    line-height     : 46px;
                    background-color: #0D3840;
                    border-radius   : 5px;
                    text-decoration : none;

                    img {
                        width         : 20px;
                        height        : 20px;
                        vertical-align: middle;
                    }

                    span {
                        margin-left   : 5px;
                        font-family   : Poppins;
                        font-size     : 15px;
                        font-style    : normal;
                        font-weight   : 500;
                        letter-spacing: 0em;
                        text-align    : center;

                    }
                }
            }
        }
    }
}

