.modal {
    position        : absolute;
    top             : 106px;
    overflow-y      : auto;
    position        : relative;
    margin          : 0 auto;
    padding         : 25px;
    width           : 586px;
    min-width       : 586px;
    background-color: #01191E;
    border-radius   : 10px;
    border          : none !important;
    outline         : none;

    button.modal_close {
        position  : absolute;
        top       : 25px;
        right     : 25px;
        background: none;
        border    : none;
        cursor    : pointer;

        i {
            display  : block;
            color    : rgba(255, 255, 255, 0.5);
            font-size: 30px;
        }
    }

    .modal_outer {

        text-align: center;


        .modal_title {
            margin        : 0px 25px 25px 25px;
            font-family   : Poppins;
            font-size     : 23px;
            font-style    : normal;
            font-weight   : 700;
            line-height   : 35px;
            letter-spacing: 0em;
            text-align    : center;

        }

        .modal_content {
            padding         : 10px;
            background      : #01060A;
            border-radius   : 10px;
            // min-height   : 687px;
            // overflow: scroll;
            text-align      : left;
            max-height      : 400px;
            overflow-y      : auto;
            scrollbar-color : #d4aa70 #e4e4e4;
            scrollbar-width : thin;

            p {
                margin-bottom: 20px;
                font-size    : 16px;
            }

            &::-webkit-scrollbar {
                width: 20px;
            }

            &::-webkit-scrollbar-track {
                background-color: unset;
                border-radius   : 100px;
            }

            &::-webkit-scrollbar-thumb {
                background-color: #01191E;
                border          : 6px solid rgba(0, 0, 0, 0);
                background-clip : padding-box;
                border-radius   : 9999px;
            }
        }
    }

}